import { GeneralService, HttpService } from './api';
import { GeneralStore } from './general.store';

/**
 * Root Store Class with
 */
class RootStore {
  private http = new HttpService(
    () => ({
      baseURL: 'https://anrf8hxs1l.execute-api.eu-central-1.amazonaws.com/DEV'
    }),
    {}
  );

  public api = {
    general: new GeneralService(this.http)
  };
  general: GeneralStore;

  constructor() {
    this.general = new GeneralStore(this);
  }
}

const store = new RootStore();

export { RootStore, store };
